import React, {Component} from 'react';
import * as Consts from '../../../../constants/consts';

const styles = {
  imglock: {
    paddingTop: 20
  },
};

class HouseImage extends Component {

  output() {
    const imageHouseUrl = Consts.blobUrl + '/houseimage';
    
    return this.props.houseImage ? `${imageHouseUrl}/${this.props.houseImage}.jpg` : '';
  }

  setWidth() {
    return this.props.houseImage ? 275 : 0;
  }

  setHeight() {
    return this.props.houseImage ? 185 : 0;
  }

  render() {
    return (
      <div style={styles.imglock}>
        <p><img src={this.output()} width={this.setWidth()} height={this.setHeight()} /></p>
      </div>
    );
  }
}

module.exports = HouseImage;



