import React, {Component} from 'react';
import * as Consts from '../../../../constants/consts';

const styles = {
  imglock: {
    paddingTop: 20
  },
};

class PlanImage extends Component {

  output() {
    const imagePlanUrl = Consts.blobUrl + '/floorimage';
    
    return this.props.planImage ? `${imagePlanUrl}/${this.props.planImage}.svg` : '';
  }

  setWidth() {
    return this.props.planImage ? 275 : 0;
  }

  setHeight() {
    return this.props.planImage ? 185 : 0;
  }

  render() {
    return (
      <div style={styles.imglock}>
        <p><img src={this.output()} width={this.setWidth()} height={this.setHeight()} /></p>
      </div>
    );
  }
}

module.exports = PlanImage;



